<template>
  <div>
    <div class="bg-top"></div>
    <div class="bg-bottom"></div>
    <div class="login-title">
      <h1>智慧大数据物联网平台</h1>
      <span>INTELLIGENT EMERGENCY PLATFORM</span>
    </div>
    <div class="main53d">
      <img src="@/assets/img/11.png">
      <!-- <img src="@/assets/img/22.png"> -->
      <div class="login-box">
        <form id="formLogin">
          <ul>
            <li class="square">
              <i class="iconfont iconuser"></i>
              <input type="text" class="input-normal" name="username" v-model="userName" /></li>
            <li class="square">
              <i class="iconfont iconmima"></i>
              <input type="password" class="input-normal" name="password" v-model="password" /></li>
            <li class="operate-col">
              <label for="remember"><input type="checkbox" id="remember" class="icon-check">记住密码</label>
              <span>忘记密码？</span>
            </li>
            <li><button type="button" class="btn-login-normal" id="btnLogin" @click="login">登录</button></li>
          </ul>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { setItem, getItem } from '@/utils/funcs'
import constant from '@/common/constant'
import permissionUtil from '@/utils/permission_util'
import msgUtil from '@/utils/msgUtil'

export default {
  data() {
    return {
      userName: '',
      password: ''
    }
  },
  methods: {

    ...mapActions([
      'handleLogin'
    ]),
    async login() {
      const userinfo = {
        username: this.userName,
        password: this.password
      }
      try {
        const data = await this.handleLogin(userinfo)
        setItem(constant.authPermissions, data.permissions.join(','))
        setItem(constant.sysuser, data.mid)
        setItem(constant.username, data.username)
        setItem(constant.token, data.api_token)
        setItem(constant.role, data.role)
        setItem(constant.orgType, data.company_type)
        setItem(constant.code, data.code)
        setItem(constant.level, data.level)
        setItem(constant.isSuperUser, permissionUtil.hasPermission(['ADM'], [data.role]) ? 1 : 0)
        setItem(constant.isSuperAdmin, permissionUtil.hasPermission(['ADM'], [data.role]) && data.company_type === 'HQ' && !data.province ? 1 : 0)
        data.province && setItem(constant.province, data.province)
        data.city && setItem(constant.city, data.city)
        data.county && setItem(constant.county, data.county)
        data.village && setItem(constant.village, data.village)
        setItem(constant.pYCity, data.py_city)
        setItem(constant.pYProvince, data.py_province)
        setItem(constant.pYCounty, data.py_county)
        setItem(constant.pYVillage, data.py_village)
        const sysCustom = data.custom || {}
        // sysCustom.SysLogo && localStore.setItem(constant.logo, sysCustom.SysLogo)
        // sysCustom.SysLogo && localStore.setItem(constant.logoMini, sysCustom.SysLogo)
        sysCustom.SysTitle && setItem(constant.companyName, sysCustom.SysTitle)
        document.title = sysCustom.SysTitle || '应急信息管理平台'
        setItem(constant.companyTureName, data.company)
        sysCustom.SysTitle && (document.title = sysCustom.SysTitle)
        sysCustom.ExtUrl && (setItem(constant.extUrl, sysCustom.ExtUrl))
        setItem(constant.companyId, data.cid)
        console.log('城市 ------------->', getItem(constant.pYCity),
          getItem(constant.role),
          getItem(constant.province),
          getItem(constant.city),
          getItem(constant.county),
          getItem(constant.village))

        this.$router.push({
          name: 'home'
        })
      } catch (error) {
        msgUtil.error('登录失败', msgUtil.findErrorMsg(error))
      }
    }
  }
}
</script>


<style lang='less'>
@import  "../../assets/css/login.css";
</style>
